const fetchCurrentWeather = ({ authorizationKey, locationName }) => {
  // //true
  //   setWeatherElement((prevState) => ({
  //     ...prevState,
  //     isLoading: true,
  //   }));

  const url = `https://opendata.cwb.gov.tw/api/v1/rest/datastore/O-A0003-001?Authorization=${authorizationKey}&locationName=${locationName}`;

  return fetch(url)
    .then((response) => response.json())
    .then((data) => {
      console.log('-data', data);
      //  STEP 1 `locationData` 
      const locationData = data.records.location[0];

      // STEP 2WDSDTEMP
      const weatherElements = locationData.weatherElement.reduce(
        (neededElements, item) => {
          if (['WDSD', 'TEMP'].includes(item.elementName)) {
            neededElements[item.elementName] = item.elementValue;
          }
          return neededElements;
        },
        {}
      );

      // STEP 3 React 
      //  setWeatherElement((prevState)=>({
      //   ...prevState,
      //    observationTime: locationData.time.obsTime,
      //    locationName: locationData.locationName,
      //    temperature: weatherElements.TEMP,
      //    windSpeed: weatherElements.WDSD,
      //    isLoading: false,
      //  }));

      return {
        observationTime: locationData.time.obsTime,
        locationName: locationData.locationName,
        temperature: weatherElements.TEMP,
        windSpeed: weatherElements.WDSD,
      };
    });
};

const fetchWeatherForecast = ({ authorizationKey, cityName }) => {
  //true
  // setWeatherElement((prevState) => ({
  //   ...prevState,
  //   isLoading: true,
  // }));

  const url = `https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=${authorizationKey}&locationName=${cityName}`;

  return fetch(url)
    .then((response) => response.json())
    .then((data) => {
      console.log('- 36 data', data);
      //  STEP 1 `locationData` 
      const locationData = data.records.location[0];

      // STEP 2 'Wx', 'PoP', 'CI'
      const weatherElements = locationData.weatherElement.reduce(
        (neededElements, item) => {
          if (['Wx', 'PoP', 'CI'].includes(item.elementName)) {
            neededElements[item.elementName] = item.time[0].parameter;
          }
          return neededElements;
        },
        {}
      );

      return {
        description: weatherElements.Wx.parameterName,
        weatherCode: weatherElements.Wx.parameterValue,
        rainPossibility: weatherElements.PoP.parameterName,
        comfortability: weatherElements.CI.parameterName,
      };
    });
};

