const useWeatherAPI = ({ authorizationKey, cityName, locationName }) => {
  //
  const [weatherElement, setWeatherElement] = useState({
    observationTime: new Date(),
    locationName: '',
    temperature: 0,
    windSpeed: 0,
    description: '',
    weatherCode: 0,
    rainPossibility: 0,
    comfortability: '',
    isLoading: true,
  });

  //useCallbackfetchData
  const fetchData = useCallback(async () => {
    //true
    setWeatherElement((prevState) => ({
      ...prevState,
      isLoading: true,
    }));

    const [currentWeather, weatherForecast] = await Promise.all([
      fetchCurrentWeather({ authorizationKey, locationName }),
      fetchWeatherForecast({ authorizationKey, cityName }),
    ]);

    // console.log('-&- 36 ',data);
    setWeatherElement({
      ...currentWeather,
      ...weatherForecast,
      isLoading: false,
    });
  }, [authorizationKey, cityName, locationName]);

  //useEffectfetchData

  // componentDidMountGET
  useEffect(() => {
    fetchData();
  }, [fetchData]);

  //Custom Hooks
  return [weatherElement, fetchData];
};

export default useWeatherAPI;
